import React, { Component } from "react";
import "../../style/team.css";
//import ReactCardFlip from 'react-card-flip';
import { TEAM } from "../../assets/prof";
import ReadMoreReact from 'read-more-react';

import {
  Card,
  CardImg,
  CardSubtitle,
  Button,
  CardImgOverlay,
  CardText,
  CardBody,
  CardTitle
} from "reactstrap";

class Team extends Component {
  constructor(props) {
    super(props);
    this.state = { team: TEAM };
    
  }

  render() {
    
    const professor = this.state.team.map(d => {
      return (
        //   <div className={styles['card-container']}>
        //  <UserCard
        //   header="../../back_prof.jpg"
        //   avatar="../../1.jpg"
        //   name={d.name}
        //   positionName={d.Education}

        //  />
        //  </div>
        <div key={d.id} className="col-md-4 col-12">
          <div className="cont">
          <div class="card" style={{marginTop:"10px"}}>
            <img
              className="team_img"
              src={d.image}
              alt="Jane"
              style={{ width: "50%","alignSelf":"center" }}
            />
            <h4 style={{ color: "white" }}>{d.name}</h4>
            <p className="title" style={{color:"white"}, {"fontSize":"20px"}}>{d.Designation}</p>
            
            <ReadMoreReact text={d.description} max={100} readMoreText="more..."/>
            
            
            
          </div>
          </div>
        </div>
      );
    });
    return <div className="row" id="Team">{professor}</div>;
  }
}






export default Team;
